import React, { ReactNode, useState } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import './DeckSlider.scss';

// interface Props {
// 	children?: ReactNode;
// 	initialPoint?: number;
// 	endPoint?: number;
// }

const imageData = [
	{
		imgUrl: '/img/exhibition/img-gallery-1.jpg',
	},
	{
		imgUrl: '/img/exhibition/img-gallery-2.jpg',
	},
	{
		imgUrl: '/img/exhibition/img-gallery-1.jpg',
	},
];

export const DeckSlider = (
	// { children, initialPoint, endPoint }: Props,
) => {
	const [animating, setAnimating] = useState<boolean>(false);
	const [activeSlide, setActiveSlide] = useState<number>(1);
	const [prevSlide, setPrevSlide] = useState<number | null>(null);

	const toSlide = (targetSlide: number) => {
		setAnimating(true);
		setPrevSlide(activeSlide);
		setActiveSlide(targetSlide);
		setTimeout(() => {
			setPrevSlide(null);
		}, 300);
		setTimeout(() => {
			setAnimating(false);
		}, 600);
	};

	// Scroll Monitor
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});
	const processPercentage = parseFloat(percentage.toPrecision(2));

	return (
		<div ref={ref} className={`deck-slider ${animating ? 'is-animating' : ''}`}>
			<div className="deck-slider__slider">
				{
					imageData.map((item: any, index: number) => {
						const adjIndex = index + 1;
						const key = `gallery-${adjIndex}`;
						const isActive = (activeSlide === adjIndex);
						const isPrev = (prevSlide === adjIndex);
						let order = adjIndex - activeSlide;
						if (order < 0) {
							order = imageData.length + order;
						}
						return (
							<div
								key={key}
								className={`deck-slider__slide ${isActive ? 'is-active' : ''} ${isPrev ? 'is-prev' : ''}`}
								style={{
									right: `-${(10 * order)}%`,
									zIndex: imageData.length - order,
									order,
								}}
							>
								<div
									className="deck-slider__slide-body"
									style={{
										transform: `scale(${1 * (0.7 ** order)})`,
									}}
								>
									<button
										type="button"
										className="deck-slider__click-area"
										onClick={() => { toSlide(adjIndex); }}
									>
										To this slide
										{' '}
										{adjIndex}
									</button>
									{/* <img className="deck-slider__img" src={item.imgUrl} alt="" /> */}
									<div
										className="deck-slider__img"
										style={{
											backgroundImage: `url(${item.imgUrl})`,
										}}
									/>
								</div>
							</div>
						);
					})
				}
			</div>
			<div className="deck-slider__captions">
				<div className="deck-slider__caption">
					<div className="deck-slider__date">1 Mar–30 May 2021<br />10am to 7pm</div>
					<div className="deck-slider__location">
						HACC,
						<div className="deck-slider__addr">
							K11 Atelier King&apos;s Road
						</div>
					</div>
					<div className="deck-slider__remark">By Invitation Only</div>
				</div>
			</div>
		</div>
	);
};

export default DeckSlider;
