import React, { useEffect, useState } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import { PerspectiveBlock, ViewPortAnimation, ShapeOutText, LetterFader } from '../../components';
import './Gallery.scss';

interface Props {
	pageProgess: any;
	updatePageProgess: (status: any) => void;
}

const Gallery = ({ pageProgess, updatePageProgess }: Props) => {
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});

	const processPercentage = parseFloat(percentage.toPrecision(2));

	const sectionActive = processPercentage > 0.2;

	useEffect(() => {
		if (sectionActive && pageProgess.gallery !== 'active') {
			if (pageProgess.gallery !== 'active') {
				updatePageProgess({
					...pageProgess,
					gallery: 'active',
				});
			}
		} else if (pageProgess.gallery === 'active') {
			updatePageProgess({
				...pageProgess,
				gallery: '',
			});
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [sectionActive]);

	return (
		<>
			<div
				ref={ref}
				className={`sec-gallery ${sectionActive ? 'is-active' : ''}`}
			>
				<div
					className="sec-gallery__inner"
				>
					<h2 className="sec-gallery__title">
						<span
							className="sec-gallery__title-icon"
							style={{
								backgroundImage: 'url(/img/gallery/icon-gallery.svg)',
							}}
						/>
						<small className="sec-gallery__title-tag">EXHIBITION HIGHLIGHTS</small>
						<span className="sec-tour__title-txt">
							<ShapeOutText
								text={['GALLERY']}
							/>
							{/* <ViewPortAnimation delay={0.3}>
								<span className="sec-gallery__title-gray coming-soon-text">COMING SOON</span>
							</ViewPortAnimation> */}
						</span>
					</h2>
					{/* <div className="sec-gallery__desc">
						<p>
							The Virtual Tour of the exhibition will be available
							for visiting online here in Mid March.
						</p>
					</div> */}
					<div className="sec-gallery__gallery">
						<div className="sec-gallery__gallery-item">
							<div className="sec-gallery__gallery-item-body">
								<PerspectiveBlock>
									<div
										className="sec-gallery__gallery-graphic"
										style={{
											backgroundImage: 'url(/img/exhibition/img-gallery-1.jpg)',
										}}
									/>
								</PerspectiveBlock>
								{/* <p className="sec-gallery__item-desc">Exhibition Highlights are coming!</p> */}
							</div>
						</div>
						<div className="sec-gallery__gallery-item">
							<div className="sec-gallery__gallery-item-body">
								<PerspectiveBlock
									isReverse
								>
									<div
										className="sec-gallery__gallery-graphic"
										style={{
											backgroundImage: 'url(/img/exhibition/img-gallery-2.jpg)',
										}}
									/>
								</PerspectiveBlock>
							</div>
						</div>
					</div>

					<section className="sec-journey__subsec sec-journey__subsec--create">
						<h2 className="sec-journey__title mod-rotating">
							<span className="sec-journey__title-txt">WE CREATE.</span>
							<span className="sec-journey__effect-typing">
								<span className="sec-journey__title-txt color-main">
									<LetterFader
										options={{
											strings: [
												'INSPIRING A NEW WORLD.',
												'MAKING 50 YEARS OF LEGACY.',
												'WELCOMING AN ARTISANAL DWELLING.',
												'CREATING SHARED VALUE.',
												'SETTING THE NEW STAGE.',
											],
											autoStart: true,
											loop: true,
											cursor: '',
										}}
									/>
								</span>
							</span>
						</h2>
					</section>
				</div>
			</div>
			<div
				className="sec-gallery__bg"
			/>
		</>
	);
};

export default Gallery;
