import React, { ReactNode } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import './ViewPortAnimation.scss';

interface Props {
	children?: ReactNode;
	animation?: string;
	delay?: number;
}

export const ViewPortAnimation = (
	{ children, animation, delay }: Props,
) => {
	// Scroll Monitor
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});
	const processPercentage = parseFloat(percentage.toPrecision(2));

	return (
		<div
			ref={ref}
			className={`viewport-animtion viewport-animtion--${animation || 'default'} ${processPercentage > 0.1 ? 'is-active' : ''}`}
		>
			<div
				className="viewport-animtion__body"
				style={{
					transitionDelay: `${delay || 0}s`,
				}}
			>
				{children}
			</div>
		</div>
	);
};

export default ViewPortAnimation;
