import React, { ReactNode } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import './PerspectiveBlock.scss';

interface Props {
	children?: ReactNode;
	initialPoint?: number;
	endPoint?: number;
	isReverse?: boolean;
	offset?: number;
}

export const PerspectiveBlock = (
	{
		children, initialPoint, endPoint, isReverse, offset,
	}: Props,
) => {
	// Scroll Monitor
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});
	const processPercentage = parseFloat(percentage.toPrecision(2));

	const getTransform = () => {
		// Rotate
		const initialRotate = initialPoint || 12;
		const endRotate = endPoint || -12;
		const diff = endRotate - initialRotate;
		const isValReverse = endRotate < initialRotate;

		let rotateDegree = initialRotate + (diff * (processPercentage + (offset || 0)) * 1.25);
		if (isValReverse) {
			if (rotateDegree < endRotate) {
				rotateDegree = endRotate;
			}
			if (rotateDegree > initialRotate) {
				rotateDegree = initialRotate;
			}
		} else {
			if (rotateDegree > endRotate) {
				rotateDegree = endRotate;
			}
			if (rotateDegree < initialRotate) {
				rotateDegree = initialRotate;
			}
		}
		// const rotateDegreeX = (isReverse) ? rotateDegree : rotateDegree * 2;
		// const rotateDegreeY = (isReverse) ? rotateDegree * 2 : rotateDegree;
		const rotateDegreeX = (isReverse) ? rotateDegree : rotateDegree;
		const rotateDegreeY = (isReverse) ? 0 - rotateDegree * 2 : rotateDegree * 2;
		// if (processPercentage > 0.5) {
		// 	rotateDegree = 0;
		// }
		// transform: rotateX(16.6771deg) rotateY(8.33856deg) rotateZ(0deg);
		// transform: rotateX(-14.1093deg) rotateY(-7.05465deg) rotateZ(0deg);
		return `rotateX(${rotateDegreeX}deg) rotateY(${rotateDegreeY}deg)  rotateZ(0deg)`;
	};

	return (
		<div ref={ref} className="perspective-block">
			<div
				className="perspective-block__content"
				style={{
					transform: getTransform(),
				}}
			>
				{children}
			</div>
		</div>
	);
};

export default PerspectiveBlock;
