import React, { useEffect, useState } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import Typewriter from 'typewriter-effect';
import { PerspectiveBlock, ViewPortAnimation, ShapeOutText } from '../../components';
import './Exhibition.scss';
import DeckSlider from '../../components/DeckSlider/DeckSlider';

interface Props {
	pageProgess: any;
	updatePageProgess: (status: any) => void;
}

const Exhibition = ({ pageProgess, updatePageProgess }: Props) => {
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});

	const processPercentage = parseFloat(percentage.toPrecision(2));

	const sectionActive = processPercentage > 0.15;

	useEffect(() => {
		if (sectionActive && pageProgess.exhibition !== 'active') {
			if (pageProgess.exhibition !== 'active') {
				updatePageProgess({
					...pageProgess,
					exhibition: 'active',
				});
			}
		} else if (pageProgess.exhibition === 'active') {
			updatePageProgess({
				...pageProgess,
				exhibition: '',
			});
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [sectionActive]);


	const getOverlayOpacity = () => {
		let overlayOpacity = processPercentage * 2;
		// if (processPercentage > 0.5) {
		// 	overlayOpacity = 1 - processPercentage * 1.5;
		// }
		if (overlayOpacity > 1) {
			overlayOpacity = 1;
		}
		if (overlayOpacity < 0) {
			overlayOpacity = 0;
		}
		return `${overlayOpacity}`;
	};


	return (
		<>
			<div
				className="sec-exhibition__bg"
				style={{
					opacity: getOverlayOpacity(),
				}}
			>
				<div
					className="sec-exhibition__bg-graphic"
				/>
				<div className="sec-exhibition__bg-overlay" />
			</div>
			<div
				ref={ref}
				className={`sec-exhibition ${sectionActive ? 'is-active' : ''}`}
			>
				<div className="sec-exhibition__inner">
					<h2 className="sec-exhibition__title">
						<span
							className="sec-exhibition__title-icon"
							style={{
								backgroundImage: 'url(/img/exhibition/icon-exhibition.svg)',
							}}
						/>
						<small className="sec-exhibition__title-tag">WE CREATE. WE ARE ARTISANS. WE ARE CSV.</small>

						<span className="sec-exhibition__title-txt">
							<ShapeOutText
								text={['New World', 'Development 50th', 'Anniversary', 'Celebration']}
							/>
						</span>
					</h2>
					<div className="sec-exhibition__desc">
						<ViewPortAnimation>
							<p>
								{'It is in us to think bolder, '}
								<br />
								act kinder, and discover newer
								<br />
									in the face of an ever-changing world.
							</p>
						</ViewPortAnimation>
						{/* <div className="sec-exhibition__perspective">
							<PerspectiveBlock>
								<div className="sec-exhibition__perspective-deco" />
								<div className="sec-exhibition__perspective-content">
									<div className="sec-exhibition__perspective-bg" />
									<p className="sec-exhibition__perspective-text">ACHIEVEMENTS ARE NOTHING IF THEY FAIL TO INSPIRE THE WORLD</p>
									<p className="sec-exhibition__perspective-tag">ADRIAN CHENG</p>
								</div>
							</PerspectiveBlock>
						</div> */}
						<ViewPortAnimation>
							<p>
								{'The only true piece of great work is '}
								<br />
								{'one that inspires…therefore, '}
								<br />
								{'We Create. Inspiring a New World. '}
							</p>
						</ViewPortAnimation>
					</div>
					<div className="sec-exhibition__gallery">
						<div className="sec-exhibition__gallery-infos">
							<div className="sec-exhibition__gallery-graphic" />
							<div className="sec-exhibition__gallery-info">
								<div className="sec-exhibition__gallery-info-content">
									<img className="sec-exhibition__slogans" src="img/exhibition/logo-slogans.svg" alt="" />
								</div>
							</div>
							<div className="sec-exhibition__gallery-info">
								<div className="sec-exhibition__gallery-info-content">
									<small className="sec-exhibition__info-tag">New World Development</small>
									<span className="sec-exhibition__info-title">50th Anniversary Celebration</span>
								</div>
							</div>
						</div>
						<div className="sec-exhibition___slider">
							<DeckSlider />
						</div>
					</div>
				</div>
			</div>
		</>
	);
};

export default Exhibition;
