import React, { useEffect, useState } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import Typewriter from 'typewriter-effect';
import { ViewPortAnimation, ShapeOutText } from '../../components';
import './Tour.scss';

interface Props {
	pageProgess: any;
	updatePageProgess: (status: any) => void;
}

const Tour = ({ pageProgess, updatePageProgess }: Props) => {
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});

	const processPercentage = parseFloat(percentage.toPrecision(2));

	const sectionActive = processPercentage > 0.2;

	useEffect(() => {
		if (sectionActive && pageProgess.tour !== 'active') {
			if (pageProgess.tour !== 'active') {
				updatePageProgess({
					...pageProgess,
					tour: 'active',
				});
			}
		} else if (pageProgess.tour === 'active') {
			updatePageProgess({
				...pageProgess,
				tour: '',
			});
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [sectionActive]);

	return (
		<div
			ref={ref}
			className={`sec-tour ${sectionActive ? 'is-active' : ''}`}
		>
			<div
				className="sec-tour__inner"
			>
				<h2 className="sec-tour__title">
					<span
						className="sec-tour__title-icon"
						style={{
							backgroundImage: 'url(/img/tour/icon-tour.svg)',
						}}
					/>
					<small className="sec-tour__title-tag">EXTRAORDINARY EXPERIENCE</small>
					<span className="sec-tour__title-txt">
						<ShapeOutText
							text={['VIRTUAL', 'TOUR']}
						/>
						<ViewPortAnimation delay={0.3}>
							<span className="sec-tour__title-gray coming-soon-text">COMING SOON</span>
						</ViewPortAnimation>
					</span>
				</h2>
				<div className="sec-tour__desc">
					<ViewPortAnimation>
						<p>
							The Virtual Tour will be available for visiting online in Mid March.
						</p>
					</ViewPortAnimation>
				</div>
			</div>
		</div>
	);
};

export default Tour;
