import React, { useEffect, useState } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import Typewriter from 'typewriter-effect';
import { ViewPortAnimation } from '../../components';
import './Footer.scss';

interface Props {
	pageProgess: any;
	updatePageProgess: (status: any) => void;
}

const Footer = ({ pageProgess, updatePageProgess }: Props) => {
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});

	const processPercentage = parseFloat(percentage.toPrecision(2));

	const sectionActive = processPercentage > 0.2;

	useEffect(() => {
		if (sectionActive && pageProgess.footer !== 'active') {
			if (pageProgess.footer !== 'active') {
				updatePageProgess({
					...pageProgess,
					footer: 'active',
				});
			}
		} else if (pageProgess.footer === 'active') {
			updatePageProgess({
				...pageProgess,
				footer: '',
			});
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [sectionActive]);

	return (
		<footer
			ref={ref}
			className={`footer ${sectionActive ? 'is-active' : ''}`}
		>
			<div className="footer__top">
				<div className="footer__inner">
					<h2 className="footer__title">
						THE
						<br />
						COLLABORATIONS
					</h2>
					<div className="footer__collabs">
						<div className="footer__collab-grp">
							<h3 className="footer__collab-title">Venue Provider</h3>
							<div className="footer__logos">
								<img className="footer__logo footer__logo--k11" src="/img/logo/logo-k11.png" alt="" />
							</div>
						</div>
						<div className="footer__collab-grp">
							<h3 className="footer__collab-title">Creative Powers</h3>
							<div className="footer__logos">
								<img className="footer__logo footer__logo--ddb" src="/img/logo/logo-ddb.svg" alt="" />
								<img className="footer__logo footer__logo--msl" src="/img/logo/logo-msl.svg" alt="" />
							</div>
						</div>
					</div>
				</div>
			</div>
			<div className="footer__bottom">
				<div className="footer__inner">
					<div className="footer__nwd50">
						<div className="footer__logos">
							<img className="footer__logo footer__logo--nwd50" src="/img/logo/footer-logo.svg" alt="" />
							{/* <div className="footer__logo-br" />
							<img className="footer__logo footer__logo--slogans" src="/img/logo/logo-slogans.svg" alt="" /> */}
						</div>
					</div>
					<div className="footer__info">
						<div className="footer__copyright">{`Copyright © ${new Date().getFullYear()} New World Development. All rights reserved.`}</div>
						<div className="footer__logos">
							<img className="footer__logo footer__logo--tam" src="/img/logo/logo-tam.svg" alt="" />
						</div>
					</div>
				</div>
			</div>
		</footer>
	);
};

export default Footer;
