import React, { ReactNode } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import './ShapeOutText.scss';

interface Props {
	text: string[];
}

export const ShapeOutText = (
	{ text }: Props,
) => {
	// Scroll Monitor
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});
	const processPercentage = parseFloat(percentage.toPrecision(2));

	return (
		<div ref={ref} className={`shapeout-text ${processPercentage > 0.1 ? 'is-active' : ''}`}>
			<div
				className="shapeout-text__body"
			>
				<span className="shapeout-text__line">
					<span className="shapeout-text__shape" />
					<span className="shapeout-text__content">
						<span className="shapeout-text__txt">{text[0]}</span>
					</span>
				</span>
				{text[1] ? (
					<span className="shapeout-text__line">
						<span className="shapeout-text__shape" />
						<span className="shapeout-text__content">
							<span className="shapeout-text__txt">{text[1]}</span>
						</span>
					</span>
				) : null}
				{text[2] ? (
					<span className="shapeout-text__line">
						<span className="shapeout-text__shape" />
						<span className="shapeout-text__content">
							<span className="shapeout-text__txt">{text[2]}</span>
						</span>
					</span>
				) : null}
				{text[3] ? (
					<span className="shapeout-text__line">
						<span className="shapeout-text__shape" />
						<span className="shapeout-text__content">
							<span className="shapeout-text__txt">{text[3]}</span>
						</span>
					</span>
				) : null}
			</div>
		</div>
	);
};

export default ShapeOutText;
