import _ from 'lodash';

declare global {
	interface Window {
		dataLayer: any;
	}
}

export const trackSection = _.debounce((sectionName: string) => {
	if (window.dataLayer) {
		window.dataLayer.push({ event: 'section-view', 'section-name': sectionName });
	}
}, 250);

export default trackSection;
