import React, { useEffect, useState } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import { Link } from 'react-router-dom';
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';
// eslint-disable-next-line @typescript-eslint/ban-ts-ignore
// @ts-ignore
import ParallaxMousemove from 'react-parallax-mousemove';
import {
	Accordion, PerspectiveBlock, ViewPortAnimation, ShapeOutText,
} from '../../components';
import useWindowSize from '../../hooks/useWindowSize';
import './SubscribePopup.scss';


interface Props {
	open: boolean;
	onClosePopup: () => void;
}
const SubscribePopup = ({ open, onClosePopup }: Props) => {
	const windowSize = useWindowSize();

	useEffect(() => {
		if (open && windowSize.width < 768) {
			disableBodyScroll(document.body);
		} else {
			enableBodyScroll(document.body);
		}
	}, [open, windowSize]);

	return (
		<div
			// ref={ref}
			className={`subs-popup ${open ? 'is-open' : ''}`}
		>
			<button
				className="subs-popup__overlay"
				type="button"
				onClick={() => onClosePopup()}
			>
				Close popup
			</button>
			<div className="subs-popup__body">
				<div className="subs-popup__head">
					<button
						className="subs-popup__close"
						type="button"
						onClick={() => onClosePopup()}
					>
						Close popup
					</button>
				</div>
				<div className="subs-popup__form">
					{/* FORM HERE */}
					<iframe src="https://uat.weareartisans.hk/en/nwd-50th-anniversary" height="100%" width="100%" title="Become an artisan" />
					{/* END FORM HERE */}
				</div>
			</div>
		</div>
	);
};

export default SubscribePopup;
